import Axios from 'axios'

/**
 * api
 * @export
 * @param {page} param0 
 */
export function getMM(page){
  return Axios({
    method: 'get',
    url: `https://gank.io/api/data//10/${page}`,
  })
}

/**
 * home, 
 * @param {Object} timeObj : {Y:'2019', M: '02', D: '13' }
 */
export function getDetail(timeObj){
  return Axios({
    method: 'get',
    url: `http://gank.io/api/history/content/day/${timeObj.Y}/${timeObj.M}/${timeObj.D}`
  })
}

/**
 * 
 * @param { string } type : /iOS/Android
 * @param { Number } page 
 */
export function getListData(type, page){
  return Axios({
    method: 'get',
    url: `http://gank.io/api/data/${type}/10/${page}`
  })
}